.products-section {
  padding: 0 1rem;
}

.products-container {
  max-width: 992px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  justify-content: center;
  gap: 1rem;
  margin: 0 auto;
  display: grid;
}

.products-content {
  cursor: pointer;
  box-shadow: -3px 3px #05303ea8, -5px 5px #103c51;
}

.products-content .price-tag {
  color: #9db7c4;
  margin-left: 4px;
  font-size: 12px;
}

.products-content p {
  text-align: center;
  background: #0a4253;
  line-height: 1.5rem;
  transition: all .2s ease-out;
}

.products-content:hover > p {
  background: #0d546a;
}

.products-content .products-img {
  height: 200px;
  background: #0e1d24 center / cover no-repeat;
}

.mb-4 {
  margin-bottom: 4rem;
}

